@font-face {
  font-family: 'MADE Canvas';
  src: url("../fonts/MADECanvas-Black.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: 'MADE Canvas';
  src: url("../fonts/MADECanvas.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'MADE Canvas';
  src: url("../fonts/MADECanvas-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Raleway';
  src: url("../fonts/Raleway-ExtraLight.woff2") format("woff2");
  font-weight: 200;
  font-style: normal;
}

@font-face {
  font-family: 'Raleway';
  src: url("../fonts/Raleway-Italic.woff2") format("woff2");
  font-weight: normal;
  font-style: italic;
}

@font-face {
  font-family: 'Raleway';
  src: url("../fonts/Raleway-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

body {
  color: #707070;
}

header {
  height: 100vh;
  background: url("../img/bg_mobile.png") no-repeat top/cover;
}

.bandeNom {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  background-color: #333;
}

.bandeNom .nom {
  height: 30px;
}

.accroche {
  display: flex;
  flex-direction: column;
  background-color: #fffd;
  backdrop-filter: blur(10px);
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 250px;
  clip-path: polygon(0 30%, 100% 0, 100% 100%, 0 100%);
  padding: 24px;
}

.accroche .dev {
  align-self: flex-end;
  width: 50vw;
  max-width: 280px;
  height: calc(50vw / 2.66);
  max-height: 107px;
  margin: 35px 0 16px 0;
}

.accroche span {
  font-family: Raleway, sans-serif;
  font-size: 18px;
  line-height: 1.25;
  max-width: 520px;
}

@media screen and (min-width: 768px) {
  header {
    height: 100vh;
    background-image: url("../img/bg_tablet.png");
    background-position: right;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  .bandeNom {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
  }
  .accroche {
    position: initial;
    width: 50vw;
    max-width: 600px;
    height: 350px;
    margin-bottom: 50px;
    clip-path: polygon(0 20%, 100% 0, 100% 80%, 0 100%);
  }
  .accroche .dev {
    margin-top: 60px;
  }
}

@media screen and (min-width: 1200px) {
  .bandeNom {
    height: 80px;
  }
  .bandeNom .nom {
    height: 40px;
  }
  header {
    background-image: url("../img/bg_desktop.png");
    background-position: center;
  }
}
